import axios from 'axios';
import { MessageBox } from 'element-ui';
// axios
//  * 
//  * 401 
//  * 403 
//  * 404 
//  * 500 

// export default service

const httpCode = {
    400: '',
    401: '',
    403: '',
    404: '',
    405: '',
    408: '',
    500: '',
    501: '',
    502: '',
    503: '',
    504: '',
    505: 'http ',
};
// window.API = 'http://10.10.90.30/api/v1/BigScreen'
// window.API = 'http://localhost:8082'
window.API = '';

export default (options = {}) => {
    //  
    return new Promise((resolve, reject) => {
        let service = axios.create({
            baseURL: window.API, // apibase_url
            // baseURL: 'http://192.168.88.148:8080', // apibase_url
            timeout: 1000 * 60 * 5, // request timeout 5
        });

        // request
        service.interceptors.request.use(
            config => {
                // config.headers['Access-Control-Request-Headers'] = 'Authorization'
                config.headers['Authorization'] = window.localStorage.getItem(
                    'token',
                );
                // config.headers['Authorization'] = 'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJpc3MiOiJsaW5neGUtY2xvdWQiLCJleHAiOjE1NjE0MDc0NzQsInVzZXJJZCI6IjkxYjJmNjk4ZjI1YzRiNTNiMjJlNmY2ODM2N2QzZjJmIn0.3Z61jGNYV49QOpX70V6xTY3Vg6S8Zkd_V3K4hjGCrJg'
                config.headers['Content-Type'] =
                    'application/json; charset=UTF-8';
                return config;
            },
            error => {
                console.log(error);
                Promise.reject(error);
            },
        );

        // respone
        service.interceptors.response.use(
            response => {
                const res = response.data;
                if (res.valid) return res;
                else return Promise.reject(res);
            },
            error => {
                return Promise.reject(error);
            },
        );

        // 
        service(options)
            .then(data => resolve(data))
            .catch(error => {
                console.log(error); // for debug
                let msg = '';
                if (error.code) msg = httpCode[error.code];
                if (error.msg) msg = error.msg;

                let goLoginCode = [102, 103, 104, 105, 106, 210, 211, 212];
                if (error.code && goLoginCode.indexOf(error.code) !== -1) {
                    MessageBox.alert(msg, '', {
                        type: 'error',
                        confirmButtonText: '',
                    }).then(() => {
                        window.localStorage.clear();
                        window.location.reload();
                    });
                } else {
                    MessageBox.alert(msg, '', {
                        type: 'error',
                        confirmButtonText: '',
                        callback: action => {
                            console.log(action);
                        },
                    });
                }

                reject(error);
            });
    });
};
